.footer{
    width: 100%;
    background-image: url('./footer-bg.jpg');
    background-size: cover;
    /* height: 300px; */
    padding: 30px;
    margin-top: 50px;
}
.footer nav{
    width: 100%;
    max-width: 1300px;
    margin: auto;
    padding: 20px;
}
.footer h3{
    font-size: 3rem;
    margin-bottom: 30px;
}
.footermenu{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 10px;
    flex-wrap: wrap;
}

.links{
    display: flex;
    flex-direction: column;
    font-size: 1.2rem;
}
.links a{
    padding: 10px;
    opacity: 0.8;
}

.links a:hover{
    cursor: pointer;
    opacity: 1;
}
