.hero-slide{
   margin-bottom: 3rem;
   min-height: 100vh;
   max-height: 600px;
   position: relative;
  
}


.hero-slide .item{
    /* width: 100%; */
    padding: 4rem 0;
    width: 100%;
    position: relative;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

}

.hero-slide .item::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #313030b9;
    /* z-index: -1; */
}

 .container{
    width: 90% ;
    max-width: 1300px;
    margin: auto;
    min-height: 100vh;
    display: grid ;
    grid-template-columns: 2fr 1fr;
    justify-content: center;
    justify-items: center;
    align-items: center;
    /* margin-top: 30px ; */
    z-index: 999;
    transition: all 0.5s ease;
}
.info{
    transition: all 0.5s ease;
    margin: auto;
    width: 80%;
    z-index: 999;
    text-align: center;
    padding: 10px;

}
.info h2{
    padding: 10px;
    font-size: 3rem;
    font-weight: 600;
    text-transform: capitalize;
    
}

.overview{
    /* width: 80%; */
    margin: auto;
    font-size: 1.2rem;
    color: #fff;
    padding: 10px 0;
    /* font-weight: 600; */
}
.btns{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.active img,.active .info{
    animation: entry 0.5s ease;
}
.poster{
    align-self: center;
    margin: auto;
    width: 300px;
    transition: all 0.5s ease;
    z-index: 2;
}
.poster img{
    width: 100%;
}

@keyframes entry {
    0%{
        scale: 0;
    }
    100%{
        scale: 100%;
    }
}
