.header{
    width: 100%;
    background-color: transparent;
    padding: 10px;
    color: #fff;
    position: fixed;
    top: 0;
    z-index: 999;
}
ul{
    list-style-type: none;
}
a{
    text-decoration: none;
    color: #fff;
}
.logo{
    font-size: 2.5rem;
    font-weight: 600;
}
.hcontainer{
    padding: 10px 20px;
    max-width: 1300px;
    margin: auto;
}
.hcontainer,.header_nav,.logo{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}
.header_nav{
    font-size: 1.5rem;
}
.active{
    color: red;
}

.shrink{
    display: none;
}
@media screen and (max-width:550px) {
    .header_nav{
        position: fixed;
        bottom: 0;
        left: 0;
        padding: 10px;
        width: 100%;
        background-color: #000;
    }
}