.movie-card{
    position: relative;
    /* background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 160%; */
    border-radius: 20px;
    margin-bottom: 1rem;
    transition: all 0.3s ease;
    /* z-index: 2; */
}
.movie-card img{
    border-radius: 20px;
}
.btn{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    scale: 0;
    transition: all 0.3s ease;
    font-size: 1rem;
    /* visibility: hidden; */
}
.movie-card::before{
    content: '';
    width: 100%;
    height: 100%;
    background-color: #141414b7;
    /* z-index: 3; */
    position: absolute;
    top: 0;
    border-radius: 20px;
    visibility: hidden;
}

.movie-card:hover::before{
    visibility: visible;
}
.movie-card:hover .btn{
    scale: 1;
    /* visibility: visible; */
    /* transform: scale(1); */
    z-index: 4;
}



